    ctx.beginPath();

    /*
    if(stroke.length-1){
      ctx.moveTo(stroke[stroke.length-1].x, stroke[stroke.length-1].y);
    }
    */
    stroke.forEach((el, idx) => {
      ctx.lineTo(el.x, el.y);
      ctx.moveTo(stroke[idx].x, stroke[idx].y);
    });

    // ctx.closePath();
    // ctx.fill();
    ctx.stroke();
  }

