        */
        // bufCtx.quadraticCurveTo(p2.x, p2.y, p2.x-(d.dist*d.slopeX), p2.y-(d.dist*d.slopeY));
        // bufCtx.moveTo(this.pts[0].x, this.pts[0].y);
        // bufCtx.moveTo(p1.x, p1.y);
        // bufCtx.lineTo(p2.x, p2.y);
