        // bufCtx.lineTo(p2.x + (p2.to(p1).dist * p2.to(p1).slopeX) * 1, p2.y + (p2.to(p1).dist * p2.to(p1).slopeY) * 1);
        // bufCtx.lineTo(p2.x+p2.to(p1).dist*p2.to(p1).slopeX * 3, p2.y+p2.to(p1).dist*p2.to(p1).slopeY * 3);
        // bufCtx.closePath();
